<template>
  <div class="mb-60">
    <p class="text-center" v-if="isEditMode">Edit To Do</p>
    <p class="text-center" v-else>Add New To Do</p>

    <form>
      <div class="form-group row">
        <label for="name" class="col-sm-2 col-form-label">待辦項目</label>
        <div class="col-sm-10">
          <input v-model="todo.name" type="text" id="name" class="form-control" placeholder="輸入待辦項目...">
        </div>
      </div>
      <div class="form-group row">
        <label for="dateline" class="col-sm-2 col-form-label">
          <font-awesome-icon fixed-width class="ml-auto text-dark" size="lg" icon="calendar-alt" />
          到期日
        </label>
        <div class="col-sm-10">
          <datepicker v-model="todo.dateLine" :language="zh" :format="customFormatter" name="dateline"></datepicker>
        </div>
      </div>
      <div class="form-group row">
        <label for="group" class="col-sm-2 col-form-label">
          <font-awesome-icon fixed-width class="ml-auto text-dark" size="lg" icon="folder-open" />
          群組
        </label>
        <div class="col-sm-10">
          <select v-model="todo.groupID" class="custom-select form-control" id="group" placeholder="選擇群組...">
            <option value="0" selected>Add other...</option>
            <option value="1">One</option>
            <option value="2">Two</option>
            <option value="3">Three</option>
          </select>
          <input v-if="todo.groupID == 0" type="text" class="form-control mt-3" placeholder="輸入新增群組...">
        </div>
      </div>
      <div class="form-group row">
        <label for="secondaryTodo" class="col-sm-2 col-form-label">
          <font-awesome-icon fixed-width class="ml-auto text-dark" size="lg" icon="tasks" />
          子任務
        </label>
        <div class="col-sm-10">
          <input @keyup.enter="submit2" class="form-control mb-3" type="text" id="secondaryTodo" placeholder="新增子任務...">
          <div class="todo-row-wrap mb-2" :class="{ lock: item.isCheck }" v-for="item in todo.secondaryTodo" :key="item.id">
            <font-awesome-icon :key="item.id" class="ml-auto text-dark" size="lg" :icon="changeIconsByCheck(item.isCheck)" @click="item.isCheck = !item.isCheck"/>
            <input v-model="item.name" :key="item.id" type="text" class="pl-2" style="width: calc(100% - 30px)">
          </div>
        </div>
      </div>
      <div class="form-group row">
        <label for="name" class="col-sm-2 col-form-label">
          <font-awesome-icon fixed-width class="ml-auto text-dark" size="lg" icon="map-marker-alt" />
          地點
        </label>
        <div class="col-sm-10">
          <input v-model="todo.location" type="text" class="form-control" placeholder="輸入地點...">
        </div>
      </div>
      <div class="form-group row">
        <label for="note" class="col-sm-2 col-form-label">
          <font-awesome-icon fixed-width class="ml-auto text-dark" size="lg" icon="pen" />
          隨手紀錄
        </label>
        <div class="col-sm-10">
          <textarea v-model="todo.note" class="form-control" id="note" rows="5"></textarea>
        </div>
      </div>
      <button type="button" v-if="isEditMode" class="btn btn-primary float-right" @click="button">確認修改</button>
      <button type="button" v-else class="btn btn-primary float-right" @click="submit">我要新增</button>
    </form>
  </div>

</template>

<script>
import Datepicker from 'vuejs-datepicker'
import {zh} from 'vuejs-datepicker/dist/locale'

export default {
  props: ['id'],
  components: {
    Datepicker
  },
  data () {
    return {
      zh,
      todo: {
        id: 1,
        name: 'buy brackfast',
        dateLine: '2018/06/19',
        group: '',
        groupID: 0,
        secondaryTodo: [{
          isCheck: false,
          name: 'double egg hamburger, beef'
        }, {
          isCheck: false,
          name: 'juice'
        }, {
          isCheck: true,
          name: 'toast'
        }, {
          isCheck: true,
          name: 'milk'
        }],
        location: '中山區',
        note: 'note something',
        isImportant: false
      }
    }
  },
  computed: {
    isEditMode () {
      return this.id !== 'new'
    }
  },
  methods: {
    customFormatter (date) {
      return this.$moment(date).format('YYYY-MM-DD')
    },
    changeIconsByCheck (isCheck) {
      if (isCheck) {
        return ['fas', 'check-square']
      }
      return ['far', 'square']
    },
    submit () {
      console.log('submit')
    },
    submit2 () {
      console.log('submit2')
    }
  }
}
</script>

<style>
</style>



